import React from "react";
import './popup.css';
import { Icon } from '@iconify/react';

function Popup( props ){
    return(props.trigger)?(
        <div className="popup">
        <div className="pop-inner">
            <button className="closebtn" onClick={()=>props.setTrigger(false)}><Icon icon="emojione-monotone:cross-mark-button" inline={true} /></button>
            {props.children}
        </div>
        </div>
   ):"";
}

export default Popup;